.card{
  width: 75%;
  padding: 30px;
  border-radius: 10px;
  color: #fff;
  display: block;
  position: relative;
  background: #f00;
  margin-bottom: 20px;
  margin-left: 10%;
}

.card:before{
  content: '';
  position: absolute;
  left: -9.7vw;
  top: 50%;
  transform: translateY(-50%);
  width: max(2.5vw, 1rem);
  height: max(2.5vw, 1rem);
  border: 5px solid var(--dark-blue);
  border-radius: 50%;
}

.card .card-body:before{
  content: '';
  position: absolute;
  left: -8.2vw;
  top: 0;
  width: 0px;
  height: 110%;
  border: 2px solid var(--dark-blue);
  z-index: -1;
}

@media only screen and (max-width: 450px) {
  .card .card-body:before{
    left: -7.2vw;
  }
}

@media only screen and (max-width: 320px) {
  .card:before{
    left: -10.1vw;
  }
}

.card-title {
  font-size: 30px;
  margin-bottom: 20px;
}

.responsibility {
  padding-left: 18px;
}

.card-body h4 {
  margin-top: 5px;
  font-size: 1.25rem;
}

.card-body h2 {
  font-weight: 700;
  margin-bottom: 0px;
}